<template>
  <div class="details">
    <!-- 后退按键 -->
    <p class="back" title="后退" @click="$router.go(-1)">
      <span class="iconfont icon-31fanhui1"></span>
    </p>
    <!-- md文件 -->
    <div class="markdown-body">
      <p>
        <span>笔记时间：{{date}}</span>
        <span>标签：{{tag}}</span>
      </p>
      <!-- 动态渲染md文件组件 -->
      <component :is="parameter"></component>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      parameter: "", //文章参数
      date:'',
      tag:[]
    };
  },
  mounted() {
    // 获取文章参数  点击跳转获取参数  或者 刷新页面从url中获取参数
    this.parameter  = this.$route.query.article || location.search.split("=")[1];
    // 获取文章记录时间
    this.date = this.$route.query.date || location.search.split("=")[2];
    // 获取文章标签
    this.tag = this.$route.query.tag || location.search.split("=")[3];


  },
  methods: {},
};
</script>
<style lang="less" scoped>
.min {
  .markdown-body {
    font-size: 10px;
  }
}
.details {
  height: 100%;
  overflow: auto;
  .back {
    position: absolute;
    background: #fff;
    top: 0;
    height: 30px;
    line-height: 30px;
    text-align: center;
    width: 30px;
    background: rgb(241, 241, 241);
    border-radius: 10px;
    cursor: pointer;
    transition: 0.3s;
    &:hover {
      color: #fff;
      background: rgb(0, 60, 255);
    }
  }
  .markdown-body {
    margin-top: 30px;
    padding: 10px 30px;
    p{
      background: rgb(243, 243, 243);
      border-radius: 3px;
      overflow: hidden;
      color: rgb(105, 105, 105);
      padding: 0 10px;
      span:nth-child(1){
        float: left;
      }
      span:nth-child(2){
        float: right;
      }
      
    }
  }
}
</style>
